.app{
    height: 100vh;

    .sections{
        width: 100%;
        height: calc(100vh - 70px);
        background-color: lightgreen;
        position: relative;
        top: 70px;
        scroll-behavior: smooth;
        overflow-y: scroll;
        scroll-snap-type: y mandatory;
        scrollbar-width: none; //firefox
        &::-webkit-scrollbar{
            display: none;
            scroll-snap-type: y mandatory;
            scrollbar-width: none;
        }

        > *{
            width: 100%;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }
    }
    
}